
import Vue from "vue";
import Component from "vue-class-component";
import {Prop} from "vue-property-decorator";
@Component({
  name: "Button",
})
export default class Button extends Vue {
  @Prop({
    type: String,
    default: "button",
  }) type!: "button" | "router-link";
  @Prop({
    type: String,
    default: "/",
  }) to!: string;
  @Prop({
    type: String,
    default: null
  }) uiType!: string;
}
